.group-preview, .board-preview, .my-work {
  .group-header-color {background-color: var(--clr-disabled);}
  .task-add-group-color{background-color: var(--clr-disabled); filter:opacity(50%);}
  .group-content-header-color{background-color: var(--clr-disabled);}
  .task-preview-group-color{background-color: var(--clr-disabled);}
  .undefined,.no-background{color: #ababab; *{fill: #ababab}}
